// app/markdown-page/page.js
import fs from 'fs'
import path from 'path'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm' // 只用这个
import 'highlight.js/styles/github.css' // 只用这个

const mdFilePath = path.join(process.cwd(), 'app/_post/page.md')

export default async function MarkdownPage() {
  const mdContent = await fs.promises.readFile(mdFilePath, 'utf8')

  return (
    <div style={{ maxWidth: '800px', margin: '0 auto', padding: '2rem' }}>
      <h1>Markdown 渲染示例</h1>
      <ReactMarkdown remarkPlugins={[remarkGfm]}>
        {mdContent}
      </ReactMarkdown>
    </div>
  )
}